class Component {
	$target;
	$state;

	constructor($target) {
		this.$target = $target;
		this.setup();
		this.render();
	}
	setup() {}
	template() {
		return false;
	}
	render() {
		this.$target.innerHTML = this.template();
		hljs.initHighlightingOnLoad();
		this.setEvent();
	}
	setEvent() {}
	setState(newState) {
		this.$state = { ...this.$state, ...newState };
		this.render();
	}
}

